@import "styles/variables.scss";

.footerMenuContainer {
  width: 100%;
  height: 50%;
  color: $black;

  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    margin: 0;

    .menuTextOff {
      list-style: none;
      border: 1px solid $black;
      width: 30%;
      height: 5%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px;
      padding: 5px;
    }
  }
}

.footerMenuContainer {
  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    margin: 0;

    .menuTextOn {
      list-style: none;
      border: 1px solid $white;
      width: 30%;
      height: 5%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px;
      padding: 5px;
      color: $white;
    }
  }
}

@media screen and (min-width: $tablet-breakpoint) {
  .footerMenuContainer {
    width: 100vw;
    height: 3vh;
    background-color: $footerColor;
    z-index: 1;
    position: fixed;
    bottom: 0;

    ul {
      width: 37%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding-left: 0;

      .menuTextOff {
        font-family: $primaryFont;
        border: 0;
        height: 0;
        width: 15vw;
        z-index: 1;
        color: $black;
        font-size: 12px;
      }
      .menuTextOn {
        font-family: $primaryFont;
        border: 0;
        height: 0;
        width: 15vw;
        z-index: 1;
        color: $black;
        font-size: 12px;
      }
    }
  }
}
