@import "styles/variables.scss";

.menuContainer {
  display: flex;
  justify-content: flex-end;
}

.searchResultsList {
  margin: 0 auto 6vh;
}

.searchResultsList__link {
  text-decoration: none;
  color: black;
}

.headerContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30%;
  margin-bottom: 1rem;

  .logoContainer {
    margin-top: 0;
  }
  .filterSearchContainer {
    display: flex;
    flex-direction: column;
    
    .filtersContainer {
      display: flex;
      width: 80%;
    margin-left: 9%;
    justify-content: space-between;
    }

    .filters {
      display: flex;
      justify-content: space-between;
      padding: 0px 45px;
      height: 10%;
    }
  }
}

.paginationContainer {
  display: flex;
  justify-content: center;
  padding: 10px;
}

@media screen and (min-width: $mobile-breakpoint) {
  .headerContainer {
    .filterSearchContainer {
      .filtersContainer {
        display: flex;
        width: 80%;
        margin-left: 9%;
      }

      .filters {
        padding: 0px 100px;
      }
    }
  }

  .searchResultsList {
    width: 75%;
  }

  .paginationContainer {
    min-width: fit-content;
    max-width: 50%;
    margin: 0 auto;
  }
}

@media screen and (min-width: $tablet-breakpoint) {
  .headerContainer {
    flex-direction: row;

    .logoContainer {
      display: flex;
      align-items: flex-start;

      p {
        margin: 20px 0px 0px 5px;
        font-size: 3em;
      }
    }
    .filterSearchContainer {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;

      .filtersContainer {
        display: flex;
        width: 100%;
        margin-left: 0
      }
      .search {
        //  margin: 30px;
        margin-bottom: 0px;
        margin-left: 0px;
      }

      .filters {
        padding: 0px 30px 0px 0px;
      }
    }
  }
}
